<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card>
      <!-- 添加角色按钮 -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="showAddDialog = true"
            >添加角色</el-button
          >
        </el-col>
      </el-row>
      <!-- 角色列表 -->
      <el-row>
        <el-table :data="rolesList" border stripe height="700">
          <!-- 展开列 -->
          <el-table-column type="expand">
            <template slot-scope="scope">
              <el-row
                :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
                v-for="(item1, i1) in scope.row.children"
                :key="item1.id"
              >
                <!-- 渲染一级权限 -->
                <el-col :span="5">
                  <el-tag
                    closable
                    @close="removeRolesById(scope.row, item1.id)"
                    >{{ item1.authName }}</el-tag
                  >
                  <i class="el-icon-caret-right"></i>
                </el-col>
                <!-- 渲染二级和三级权限 -->
                <el-col :span="19">
                  <el-row
                    :class="[i2 === 0 ? '' : 'bdtop', 'vcenter']"
                    v-for="(item2, i2) in item1.children"
                    :key="item2.id"
                  >
                    <el-col :span="6">
                      <el-tag
                        type="success"
                        closable
                        @close="removeRolesById(scope.row, item2.id)"
                        >{{ item1.authName }}</el-tag
                      >
                      <i class="el-icon-caret-right"></i>
                    </el-col>
                    <el-col :span="18">
                      <el-tag
                        closable
                        @close="removeRolesById(scope.row, item3.id)"
                        type="warning"
                        v-for="item3 in item2.children"
                        :key="item3.id"
                        >{{ item3.authName }}</el-tag
                      >
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </template>
          </el-table-column>
          <!-- 索引列 -->
          <el-table-column type="index"></el-table-column>
          <el-table-column prop="roleName" label="角色名称"></el-table-column>
          <el-table-column prop="roleDesc" label="角色描述"></el-table-column>
          <el-table-column prop="" label="操作" min-width="80px">
            <template slot-scope="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="showEditDialogRoles(scope.row.id)"
                >编辑</el-button
              >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="removeRolesId(scope.row.id)"
                >删除</el-button
              >
              <el-button
                type="warning"
                icon="el-icon-setting"
                size="mini"
                @click="showSetRightDialog(scope.row)"
                >分配权限</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </el-row>
    </el-card>
    <!-- 分配权限对话框 -->
    <el-dialog
      title="分配权限"
      :visible.sync="setRightDialogVisible"
      width="50%"
      @close="serRightsDialogClose"
    >
      <el-tree
        :data="rightslist"
        show-checkbox
        node-key="id"
        :props="treeProps"
        :default-expand-all="true"
        :default-checked-keys="defKey"
        ref="treeRef"
      >
      </el-tree>
      <!--  -->
      <span slot="footer" class="dialog-footer">
        <el-button @click="setRightDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="allotRoles"
          >确 定</el-button
        >
      </span>
    </el-dialog>

    <!-- 编辑 -->
    <el-dialog title="提示" :visible.sync="editDialogRoles" width="30%" @close="editDialogRolesClosed">
      <el-form :model="editFormRoles" ref="editFormRolesRef" status-icon :rules="editRolesRules">
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="editFormRoles.roleName" clearable></el-input>
        </el-form-item>
        <el-form-item label="角色描述">
          <el-input v-model="editFormRoles.roleDesc" clearable></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogRoles = false">取 消</el-button>
        <el-button type="primary" @click="editRolesInfo">确 定</el-button>
      </span>
    </el-dialog>


    <!-- 添加角色用户 -->
    <el-dialog title="添加角色用户" :visible.sync="showAddDialog" width="30%">
      <!-- 添加列表 -->
      <el-form :model="addroleslist" ref="addroleslistRef" status-icon >
        <el-form-item label="角色名称">
          <el-input v-model="addroleslist.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述">
          <el-input v-model="addroleslist.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showAddDialog = false">取 消</el-button>
        <el-button type="primary" @click="addSetShowDialoglist"
          >确 定</el-button
        >
      </span>
    </el-dialog>


  </div>
</template>

<script>
export default {
  data() {
    return {
      rolesList: [],
      dialogRoles: false,
      showAddDialog: false,
      setRightDialogVisible: false,
      // 修改角色对话框
      editDialogRoles:false,
      // 添加角色表单
      addroleslist: {},
      rightslist: [],
      // 修改角色表单
      editFormRoles:{},
      treeProps: {
        label: 'authName',
        children: 'children'
      },
      defKey: [],
      editRolesRules:{
         roleName: [
          { required: true, message: '请输入角色名', trigger: 'blur' },
          { min: 1, message: '不能为空', trigger: 'blur' }
        ],
        
      },
      // 当前即将分配角色权限的id值
      roleId:''

      
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
    async getRolesList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('获取数据失败')
      this.rolesList = res.data
      //  console.log(res)
    },
    //  根据ID删除角色
    async removeRolesId(id) {
      const confirmResult = await this.$confirm(
        '此操作将永久删除该用户, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)

      if (confirmResult !== 'confirm') return this.$message.info('已取消删除！')
      const { data: res } = await this.$http.delete(`roles/${id}`)
      if (res.meta.status !== 200) return this.$message.error('删除角色失败')
      this.$message.success('删除角色成功')
      this.getRolesList()
    },
    // 根据id删除对应的权限
    async removeRolesById(role, rightId) {
      // 弹窗提示
      const confirmResult = await this.$confirm(
        '此操作将永久删除该用户权限, 是否继续?',
        '提示',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('已取消删除！')
      }
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) {
        return this.$message.error('删除角色失败')
      }
      this.$message.success('删除权限成功')
      // this.getRolesList()
      role.children = res.data
    },
    // 添加角色用户
  async addSetShowDialoglist() {
    // 校验
    const {data:res}  = await this.$http.post('roles',this.addroleslist)
    if(res.meta.status !==201) return this.$message.error("添加角色失败")
    this.$message.success("添加角色成功")
    this.showAddDialog = false;
    this.getRolesList()
  },
  // 根据ID获取要修改的数据
  async showEditDialogRoles(id){
    const {data:res}  = await this.$http.get(`roles/${id}`)
    if(res.meta.status !==200) return this.$message.error("查询角色信息失败")
    this.editDialogRoles = true
    this.editFormRoles = res.data

  },
  // 提交修改的数据
  editRolesInfo(){
    this.$refs.editFormRolesRef.validate(async valid=>{
      if(!valid) return this.$message.error("修改角色失败，请按要求填写！")
      const {data:res} = await this.$http.put(`roles/${this.editFormRoles.roleId}`,{roleName:this.editFormRoles.roleName,roleDesc:this.editFormRoles.roleDesc})
      if (res.meta.status !== 200){
          this.$message.error('修改角色失败')
      } 
        this.$message.success('修改角色成功')
        this.editDialogRoles = false
        this.getRolesList()
      // console.log(this.editFormRoles)
    })
  },
  editDialogRolesClosed(){
      this.$refs.editFormRolesRef.resetFields()
  },
    async showSetRightDialog(role) {
      // 获取所有权限列表
      this.roleId = role.id;
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error('获取数据失败')
      this.rightslist = res.data
      this.setRightDialogVisible = true
      // 获取三级权限的id
      this.getLeafKeys(role, this.defKey)
    },
    // 通过递归的形式，获取三级权下的id，并保存到defKey数组中
    getLeafKeys(node, arr) {
      // 如果当前node节点不包含children属性，则是三级权限
      if (!node.children) {
        return arr.push(node.id)
      }

      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
      // 分配角色初始化
    serRightsDialogClose() {
      this.defKey = []
    },
    // 
    // 点击提交分配的权限
    async allotRoles(){
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedNodes()
      ]
      const idStr = keys.join(',')
      const {data:res} =await this.$http.post(`roles/${this.roleId}/rights`,{rids:idStr})
      if(res.meta.status !== 200) return this.$message.error("分配权限失败")
      this.$message.success("提交数据成功")
      this.setRightDialogVisible = false
      this.getRolesList()
    },
  
  // ____________
  },

}
</script>

<style lang="less" scoped>
.el-tag {
  margin: 10px;
}
.bdtop {
  border-top: 1px solid #eee;
}

.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
.el-tree{
  overflow-y: scroll;
  overflow: hidden;
  max-height: 400px;
  height: 300px;
}
</style>